.rmdb-searchbar {
  width: 100%;
  height: 85px;
  background: black;
  position: relative;
  padding: 15px 20px 0px 20px;
  box-sizing: border-box;
  color: #fff;
}

.rmdb-searchbar-content {
  max-width: 1280px;
  width: 100%;
  height: 55px;
  background:#1a1b1d;
  margin: 0 auto;
  border-radius: 40px;
  position: relative;
  color: #fff;
}

.rmdb-fa-search {
  position: absolute;
  left: 20px;
  top: 12px;
  color: #fff;
}

.rmdb-searchbar-input {
  font-family: 'Abel', sans-serif;
  font-size: 38px;
  position: relative;
  margin: 8px 0 0 60px;
  border: 0;
  background: transparent;
  height: 40px;
  width: 90%;
  color: #fff;
}

.rmdb-searchbar-input:focus {
  outline: none;
}

@media screen and (max-width: 720px) {
  .rmdb-searchbar {
    width: 100%;
    height: 75px;
  }

  .rmdb-searchbar-content {
    max-width: 1280px;
    width: 100%;
    height: 45px;
  }


.rmdb-fa-search {
  position: absolute;
  left: 20px;
  top: 6px;
}
.rmdb-searchbar-input {
    margin: 4px 0 0 60px;
    font-size: 24px;
    color: #fff;
    
  }
}